import React from 'react';
// import classnames from 'classnames';
import Validator from 'validator';
import isEmpty from 'lodash/isEmpty';

import TextFieldGroup from '../../common/TextFieldGroup';

// console.log(resolvedSchema);

class DateTypeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      errors: {}
    };
  }

  onChange = (e) => {
    this.props.handleOnChange(e);
  }

  onClickClearForm = (e) => {
    this.props.handleClearForm(e);
  }

  isValid() {
    const {errors, isValid} = validateInput(this.state);
    if (!isValid) {
      this.setState({errors});
    }
    return isValid;
  }

  onSubmit = (e) => {
    e.preventDefault();

    // if (this.isValid()) {
    //   this.setState({errors: {}, isLoading: true});
    //   this.setState({isLoading: false});
    // }
  }
  onClickValidate = () => {
    console.log('onClickValidate');
    const result = validateInput(this.props.data);
    this.setState({
      ...result
    })
    console.log('results: ', result);
  }

  render() {
    let {value} = this.props.data;
    let isLoading = false;
    return (
      <form onSubmit={this.onSubmit}>

        <TextFieldGroup label="Value" onChange={this.onChange}
          value={value} title="Required value for the date."
          field="value" error={this.state.errors.value || ''}/>

        <div className='btn-group'>
          <hr/>
          <button disabled={isLoading} onClick={this.onClickClearForm} className="btn btn-primary btn-sm" style={{
            marginRight: "15px"
          }}>
            <span className="glyphicon glyphicon-trash" aria-hidden="true"></span>
            Clear Form
          </button>
          <button onClick={this.onClickValidate} className="btn btn-primary btn-sm">
            <span className="glyphicon glyphicon-check" aria-hidden="true"></span>
            Validate Now
          </button>
        </div>
      </form>

    );
  }
}
function validateInput(data) {
  const errors = {};

  if (Validator.isEmpty(data.value)) {
    errors.value = 'This field is required';
  }
  // if (Validator.isISO8601(data.value)) {
  //   errors.value = 'Invalid 8601 date.'
  // }
  // if (dateParse(data.value)) {
  //   errors.value = "Fails Date.parse() test."
  // }
  if (dateParse2(data.value)) {
    errors.value = "Fails timestamp = Date.parse() test."
  }

  return {errors, isValid: isEmpty(errors)};
}

// function dateParse(data) {
//   if (Date.parse(data)) {
//     return true;
//   } else {
//     return false;
//   }
// }

function dateParse2(data){
  var timestamp=Date.parse(data)

  if (isNaN(timestamp)===false)
  {
    var d=new Date(timestamp);
    console.log('value of d:', d);
    return true;
  } else
  {
    return false;
  }
}

export default DateTypeForm;



