import React from 'react';
import PropTypes from 'prop-types';

import Validator from 'validator';
import isEmpty from 'lodash/isEmpty';
import TextFieldGroup from '../../common/TextFieldGroup';
// var resolvedSchema = require('json-schema-loader!../../../../bower_components/schema/common/json/communication/WebType.json');

// console.log(resolvedSchema);

class PhoneTypeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ...props,
      isLoading: false,
      errors: {}
    };
  }
  onChange = (e) => {
    this.props.handleOnChange(e);
  }

  onClickClearForm = (e) => {
    this.props.handleClearForm(e);
  }

  isValid = () => {
    const {errors, isValid} = validateInput(this.state);
    if (!isValid) {
      this.setState({errors});
    }
    return isValid;
  }

  onClickValidate = () => {
    const result = validateInput(this.props.data);
    this.setState({...result})
  }

  onSubmit(e) {
    e.preventDefault();

    // if (this.isValid()) {
    //   this.setState({errors: {}, isLoading: true});
    //   this.setState({isLoading: false});
    // }
  }

  render() {
    let { isLoading } = this.state;
    let { countryDialingCode, areaDialingCode, dialNumber, phoneExtension,
      access, formattedNumber } = this.props.data;

    return (
      <form onSubmit={this.onSubmit}>
        <TextFieldGroup
          label="formattedNumber"
          onChange={this.onChange}
          value={formattedNumber}
          field="formattedNumber"
          error={this.state.errors.formattedNumber || ''}
        />
        <TextFieldGroup label="countryDialingCode"
          onChange={this.onChange}
          value={countryDialingCode}
          field="countryDialingCode"
          error={this.state.errors.countryDialingCode || ''}
        />
        <TextFieldGroup label="areaDialingCode"
          onChange={this.onChange}
          value={areaDialingCode}
          field="areaDialingCode"
          error={this.state.errors.areaDialingCode || ''}
        />

        <TextFieldGroup label="dialNumber"
          onChange={this.onChange}
          value={dialNumber}
          field="dialNumber"
          error={this.state.errors.dialNumber || ''}
        />
        <TextFieldGroup label="phoneExtension" onChange={this.onChange}
          value={phoneExtension} field="phoneExtension"
          error={this.state.errors.phoneExtension || ''}
        />
        <TextFieldGroup label="access" onChange={this.onChange}
          value={access} field="access"
          error={this.state.errors.access || ''}
        />

        <div className='btn-group'>
          <hr/>
          <button disabled={isLoading} onClick={this.onClickClearForm}
            className="btn btn-primary btn-sm" style={{marginRight: "15px"}}>
            <span className="glyphicon glyphicon-trash" aria-hidden="true"></span> Clear Form
          </button>
          <button onClick={this.onClickValidate} className="btn btn-primary btn-sm">
            <span className="glyphicon glyphicon-check" aria-hidden="true"></span> Validate Now
          </button>
        </div>

      </form>

    );
  }
}
// countryDialingCode: "",
// areaDialingCode: "",
// dialNumber: "",
// phoneExtension: "",
// access: "",
// formattedNumber: ""
function validateInput(data) {
  let errors = {};

  if (!Validator.isEmpty(data.formattedNumber)) {
    errors.formattedNumber = 'formattedNumber is required.';
  }

  return {errors, isValid: isEmpty(errors)};
}

PhoneTypeForm.propTypes = {
  countryDialingCode: PropTypes.string,
  areaDialingCode: PropTypes.string,
  dialNumber: PropTypes.string,
  phoneExtension: PropTypes.string,
  access: PropTypes.string,
  formattedNumber: PropTypes.string
};
export default PhoneTypeForm;



