import React from 'react';

import AddressTypeForm from './AddressTypeForm';
import DropLoader from '../../utils/DropLoader';
import ShowJsonText from '../../common/ShowJsonText';

class AddressTypePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {
        "line": "123 Main Street",
        "city": "Philadelphia",
        "countryCode": "US"
      },
      loading: false,
      errors: []
    };
  }

  handleOnChange = (e) => {
    this.setState({
      data: {
        ...this.state.data,
        [e.target.name]: e.target.value
      }
    });
  }

  handleClearForm = () => {
    var data = {
        line: '',
        extendedLines: [''],
        countryCode: '',
        countrySubDivisions: [''],
        city: '',
        postalCode: '',
        postOfficeBox: '',
        geoLocation: {},
        formattedAddress: ''
      }
    data = { value: "" };
    this.setState({ data: data });
  }

  handleDropFileInput = (data) => {
    this.setState({data: data});
  }

  render() {
    return (
      <div>
        <div className='row text-center'>
          <h2>AddressType</h2>
          <p>[Description goes here.]</p>
          <hr/>
        </div>

        <div className="row">
          <div className="col-md-9 well">
            <AddressTypeForm
              handleOnChange={this.handleOnChange}
              handleClearForm={this.handleClearForm}
            {...this.state} />
          </div>
          <div className="col-md-3 well">
            <ShowJsonText value={this.state.data}/>
          </div>
          <DropLoader onChange={this.handleDropFileInput}/>
        </div>
      </div>
    );
  }
}

export default AddressTypePage;



