import React from 'react';
import aboutJson from '../appData/about.json';

// https://blog.haroen.me/json-ld-with-react
const JsonLd = ({ data }) =>
  <script type="application/ld+json">
    {JSON.stringify(data)}
  </script>;

const About = () => (
  <div className='row'>
    <div className='col-md-12 col-sm-6'>
      <div className='panel panel-default'>
        <div className='panel-heading'>
          <h2>About</h2>
        </div>
        <div className='panel-body'>
          <p>Interesting about-info here.</p>
          <ul>
            <li>
              <a href='https://github.com/chrispauley/hros_common'>GitHub Repo</a>
            </li>
            <li>
              <a href='https://chrispauley.github.io/hros_common/index.html'>This Web Application</a>
            </li>
            <li>
              <a href='http://www.hrvendornews.com/?p=7237'>HR Open Standards Consortium Announces 4.1 HR-JSON Assessments and Recruiting Standards</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <JsonLd data={aboutJson} />
  </div>
);

export default About;



