import React from 'react';
import PropTypes from 'prop-types';

import DateTypeForm from './DateTypeForm';
import DropLoader from '../../utils/DropLoader';
import ShowJsonText from '../../common/ShowJsonText';
// import SchemaViewer from '../../common/SchemaViewer.js';

// const resolvedSchema = require('json-schema-loader!../../../schema/common/json/base/DateType.json');


class DateTypePage extends React.Component {
  constructor(props) {
      super(props);
      this.state = {
        ...props,
        data: { value: ""}
      }
  }
  handleOnChange = (e) => {
    this.setState({
      data: {...this.state.data, [e.target.name]: e.target.value}
    });
  }

  handleClearForm = () => {
    var data = { value: "" };
    this.setState({ data: data });
  }

  handleDropFileInput = (data) => {
    this.setState({ data: data });
  }


  render() {
    return (
      <div>

        <div className='row text-center'>
          <h2>DateType</h2>
          <p>[This is inherently difficult to test for ISO 8601 compliance.]</p>
          <a target='_new'
            href='https://github.com/abritinthebay/datejs/blob/master/specs/ISOparsing-spec.js'>Spec</a>
            <hr/>
        </div>
        <div className="row">
          <div className="col-md-6 col-lg-6 well bg-success">
            <DateTypeForm
              handleOnChange={this.handleOnChange}
              handleClearForm={this.handleClearForm}
              {...this.state} />
          </div>
          <div className="col-md-3 well">
            <ShowJsonText value={this.state.data} />
          </div>
          <DropLoader onChange={this.handleDropFileInput}/>
          {/* <SchemaViewer value={resolvedSchema} /> */}
        </div>
      </div>
    );
  }
}
DateTypePage.propTypes = {
  value: PropTypes.string
};

export default DateTypePage;



