import React from 'react';
import classnames from 'classnames';
import TextFieldGroup from '../../common/TextFieldGroup';
import axios from 'axios';
// var resolvedSchema = require('json-schema-loader!../../../schema/common/json/base/MeasureType.json');

// console.log(resolvedSchema);
// console.log(resolvedSchema.properties.unitCode);

class MeasureTypeForm extends React.Component {
  constructor() {
      super();
      this.state = {
        value: '',
        unitCode: ''
      };
      this.onChange = this.onChange.bind(this);
  }

  componentDidMount() {
    axios
      .get('http://hropen.us-west-2.elasticbeanstalk.com/schema-dev/json/common/base/MeasureType#')
      .then(({ data })=> {
        console.log(data);
      	this.setState({
          schema: data
        });
      })
      .catch((err)=> {})
      console.log('axios', this.state.data);
  }

  onChange(e) {
    var u = e.target.value.toUpperCase();
    this.setState({ [e.target.name]: u });
  }

  render() {
    // const _enum = resolvedSchema.properties.unitCode.enum;
    // const options = resolvedSchema.properties.unitCode.enumNames.map(function(val,i){
    //   return (
    //     <option key={_enum[i]} value={_enum[i]}>{val}</option>
    //   );
    // });
    // console.log('enumNames', enumNames);
      const options = null;
      const desc =  null;
      return (
          <div>
            <h2>resolvedSchema.title</h2>
            <p>{desc}</p>

            <TextFieldGroup
              label="Value"
              placeholder=""
              onChange={this.onChange}
              value={this.state.value}
              field="value"
            />
            <div className={classnames('form-group')}>
              <label className="control-label">enumName</label>
              <select
                className='form-control'
                name='enumName'
                onChange={this.onChange}
                value={this.state.unitCode}
              >
                <option value='' disabled>Choose the unitCode Name</option>
                {options}
              </select>
            </div>
            <TextFieldGroup
              label="Unit Code"
              placeholder="DAY, WEE, MON, ANN "
              onChange={this.onChange}
              value={this.state.unitCode}
              field="unitCode"
              title="Try DAY, WEE, MON, ANN for day, week, month, or year"
              maxLength="3"
            />

          </div>

      );
  }
}

export default MeasureTypeForm;



