import React from 'react';
import {HashRouter as Router, Route, Switch} from 'react-router-dom';

// -- Import components
import Nav from '../pages/Nav';
import Footer from '../pages/Footer';
import About from '../pages/About';
// import Header from '../pages/Header';

// import Base from '../components/base';
import DateTypePage from '../components/base/DateType/DateTypePage';
import EntityTypePage from '../components/base/EntityType/EntityTypePage';
import GeoTypePage from '../components/base/GeoType/GeoTypePage';
import IdentifierTypePage from '../components/base/IdentifierType/IdentifierTypePage';
import MeasureTypePage from '../components/base/MeasureType/MeasureTypePage';
//
// // import Codelist from '../components/codelist';
// import Complex from '../components/base';
// import Competency from '../components/competency';
// import SpecifiedCompetencyTypePage from '../components/competency/SpecifiedCompetencyType/SpecifiedCompetencyTypePage';
// import Evaluation from '../components/evaluation';
//
import AddressTypePage from '../components/communication/AddressType/AddressTypePage';
import EmailTypePage from '../components/communication/EmailType/EmailTypePage';
import WebTypePage from '../components/communication/WebType/WebTypePage';
import PhoneTypePage from '../components/communication/PhoneType/PhoneTypePage';
//
// // import History from '../components/history';
// import BaseHistoryTypePage from '../components/profile/BaseHistoryType/BaseHistoryTypePage';
//
// import Meta from '../components/meta';
import PersonNameTypePage from '../components/person/personNameType/PersonNameTypePage';
//
//
// import CountryCodeList from '../components/codelist/CountryCodeList/CountryCodeListPage';
// import LanguageCodeList from '../components/codelist/LanguageCodelist/LanguageCodeListPage';
// import UnitCodeList from '../components/codelist/UnitCodeList/UnitCodeListPage';


class AppRoutes extends React.Component {

  render() {
    const containerStyle = { marginTop: '60px' };
    return (
      <Router>
        <div>
          <div id='#main'>
            <Nav />
            <div className='container' style={containerStyle}>

              <div className='row'>
                <div className="col-lg-12 col-sm-12">
                  <Switch>
                    <Route exact path='/' render={() => <h1>Home</h1>} />
                    <Route path='/about' render={() => <About />} />
                    {/* <Route path='/base' render={()=> <Base />} /> */}

                    <Route path='/base/date-type' render={(props) => <DateTypePage />} />
                    <Route path='/base/geo-type' render={(props) => <GeoTypePage />} />
                    <Route path='/base/identifier-type' render={(props) => <IdentifierTypePage />} />
                    <Route path='/base/measure-type' render={(props) => <MeasureTypePage />} />
                    <Route path='/base/entity-type' render={(props) => <EntityTypePage />} />
                    <Route path='/communication/address' render={(props) => <AddressTypePage />} />
                    <Route path='/communication/email' render={(props) => <EmailTypePage />} />
                    <Route path='/communication/webtype' render={(props) => <WebTypePage />} />
                    <Route path='/communication/phonetype' render={(props) => <PhoneTypePage />} />

                    <Route path='/person/person-name' render={(props) => <PersonNameTypePage />} />
                    <Route render={() => <h1>Page Not Found</h1>} />
                  </Switch>
                </div>
              </div>

            </div>
          </div>
          <Footer />
        </div>
      </Router>
    )
  }
}

export default AppRoutes;



