import React from 'react';
// import classnames from 'classnames';
// import Validator from 'validator';
import isEmpty from 'lodash/isEmpty';
import TextFieldGroup from '../../common/TextFieldGroup';

class IdentifierTypeForm extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        ...props,
        errors: {}
      }
    }

    onChange = (e) => {
      this.props.handleOnChange(e);
    }

    onClickClearForm = (e) => {
      this.props.handleClearForm(e);
    }

    isValid(){
      const { errors, isValid } = validateInput(this.state);
      if(!isValid) {
        this.setState( { errors });
      }
      return isValid;
    }

    onSubmit = (e) => {
      e.preventDefault();
      // if (this.isValid()) {
      //   this.setState({errors: {}, isLoading: true});
      //   this.setState({isLoading: false});
      // }
    }

    onClickValidate = () => {
      const result = validateInput(this.props.data);
      this.setState({...result})
    }


    render() {
      let { value, description, schemeId, schemeAgencyId, schemeVersionId, schemeLink, agencyUri } = this.props.data;
      let isLoading = false;
      return (
          <form onSubmit={this.onSubmit} >

            <TextFieldGroup
              label="Value"
              onChange={this.onChange}
              value={value}
              title="Required value for the identifier."
              field="value"
              error={this.state.errors.value || ''}
            />
            <TextFieldGroup
              label="Description"
              onChange={this.onChange}
              value={description}
              title="Optional description."
              field="description"
            />
            <TextFieldGroup
              label="SchemeId"
              onChange={this.onChange}
              value={schemeId}
              title="The identification of the identifier scheme"
              field="schemeId"
            />
            <TextFieldGroup
              label="SchemeAgencyId"
              onChange={this.onChange}
              value={schemeAgencyId}
              title="The identification of the agency that manages the identifier scheme"
              field="schemeAgencyId"
            />
            <TextFieldGroup
              label="SchemeVersionId"
              onChange={this.onChange}
              value={schemeVersionId}
              title="The identification of the version of the identifier scheme."
              field="schemeVersionId"
            />
            <TextFieldGroup
              label="SchemeLink"
              onChange={this.onChange}
              value={schemeLink}
              title="The URI that identifies where the identification scheme data is located."
              field="schemeLink"
            />
            <TextFieldGroup
              label="AgencyUri"
              onChange={this.onChange}
              value={agencyUri}
              title="The URI that identifies where the identification scheme is located."
              field="agencyUri"
            />

            <div className='btn-group'>
                          <hr/>
              <button disabled={isLoading} onClick={this.onClickClearForm}
                className="btn btn-primary btn-sm" style={{marginRight: "15px"}}>
                <span className="glyphicon glyphicon-trash" aria-hidden="true"></span> Clear Form
              </button>
              <button onClick={this.onClickValidate} className="btn btn-primary btn-sm">
                <span className="glyphicon glyphicon-check" aria-hidden="true"></span> Validate Now
              </button>
            </div>

          </form>
        );
    }
}


IdentifierTypeForm.defaultProps = {
  data: {
    value: '',
    description: '',
    schemeId: '',
    schemeAgencyId: '',
    schemeVersionId: '',
    schemeLink: '',
    agencyUri: ''
  }
}

export default IdentifierTypeForm;

function validateInput(data) {
  let errors = {};

  if (!data.value) {
      errors.value = 'This field is required';
  }

  return {errors, isValid: isEmpty(errors)};
}



