import React from 'react';
// import classnames from 'classnames';
import Validator from 'validator';
import isEmpty from 'lodash/isEmpty';
import TextFieldGroup from '../../common/TextFieldGroup';

class EntityTypeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: ''
    };
  }

  onChange = (e) => {
    this.props.handleOnChange(e);
  }

  onClickClearForm = (e) => {
    this.props.handleClearForm(e);
  }

  isValid() {
    const {errors, isValid} = validateInput(this.state);
    if (!isValid) {
      this.setState({errors});
    }
    return isValid;
  }

  onSubmit = (e) => {
    e.preventDefault();
    // if (this.isValid()) {
    //   this.setState({errors: {}, isLoading: true});
    //   this.setState({isLoading: false});
    // }
  }

  render() {
    let { value, description, schemeId, schemeAgencyId, schemeVersionId, schemeLink, agencyUri } = this.props.data.id;
    let { code, name } = this.props.data;
    let isLoading = false;
    return (
      <form onSubmit={this.onSubmit}>

        <TextFieldGroup label="Name" onChange={this.onChange} value={name} field="name"/>
        <TextFieldGroup label="Code" onChange={this.onChange} value={code} field="code"/>
        <hr/>
        <div>

          <h4>Id:
          </h4>
          <TextFieldGroup label="Value" onChange={this.onChange} value={value} title="Required value for the identifier." field="id.value"/>
          <TextFieldGroup label="Description" onChange={this.onChange} value={description} title="Optional description." field="id.description"/>
          <TextFieldGroup label="SchemeId" onChange={this.onChange} value={schemeId} title="The identification of the identifier scheme" field="id.schemeId"/>
          <TextFieldGroup label="SchemeAgencyId" onChange={this.onChange} value={schemeAgencyId} title="The identification of the agency that manages the identifier scheme" field="schemeAgencyId"/>
          <TextFieldGroup label="SchemeVersionId" onChange={this.onChange} value={schemeVersionId} title="The identification of the version of the identifier scheme." field="schemeVersionId"/>
          <TextFieldGroup label="SchemeLink" onChange={this.onChange} value={schemeLink} title="The URI that identifies where the identification scheme data is located." field="schemeLink"/>
          <TextFieldGroup label="AgencyUri" onChange={this.onChange} value={agencyUri} title="The URI that identifies where the identification scheme is located." field="agencyUri"/>
        </div>

        <div className='btn-group'>
          <hr/>
          <button disabled={isLoading} onClick={this.onClickClearForm} className="btn btn-primary btn-sm" style={{
            marginRight: "15px"
          }}>
            <span className="glyphicon glyphicon-trash" aria-hidden="true"></span>
            Clear Form
          </button>
          <button onClick={this.onClickValidate} className="btn btn-primary btn-sm">
            <span className="glyphicon glyphicon-check" aria-hidden="true"></span>
            Validate Now
          </button>
        </div>
      </form>

    );
  }
}
function validateInput(data) {
  let errors = {};

  if (Validator.isEmpty(data.name)) {
    errors.name = 'This field is required';
  }

  return {errors, isValid: isEmpty(errors)};
}

EntityTypeForm.propTypes = {
  // id: React.PropTypes.string,
  // name: React.PropTypes.string,
  // code: React.PropTypes.string,
  // handleSubmit: React.PropTypes.func,
};

export default EntityTypeForm;



