import React from 'react';
import PropTypes from 'prop-types';

import TextFieldGroup from '../../common/TextFieldGroup';
import Validator from 'validator';
import isEmpty from 'lodash/isEmpty';


class GeoTypeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ...props,
      isLoading: false,
      errors: {}
    };
  }

  onChange = (e) => {
    this.props.handleOnChange(e);
  }

  onClickClearForm = (e) => {
    this.props.handleClearForm(e);
  }

  isValid = () => {
    // const {errors, isValid} = validateInput(this.state);
    // if (!isValid) {
    //   this.setState({errors});
    // }
    // return isValid;
  }

  onClickValidate = () => {
    // console.log('onClickValidate');
    const result = validateInput(this.props.data);
    this.setState({...result})
    // console.log('results: ', result);
  }


  onSubmit(e) {
    e.preventDefault();

    // if (this.isValid()) {
    //   this.setState({errors: {}, isLoading: true});
    //   this.setState({isLoading: false});
    // }
  }


  render() {
    let { isLoading} = this.state;
    let {latitude, longitude, altitude } = this.props.data;

    return (
      <form onSubmit={this.onSubmit}>

        <TextFieldGroup label="Latitude"
          onChange={this.onChange}
          placeholder="http://www.example.com"
          value={latitude} field="latitude"
          type="text"
          error={this.state.errors.latitude || ''}
        />
        <TextFieldGroup label="Longitude"
          onChange={this.onChange}
          placeholder="http://www.example.com"
          value={longitude} field="longitude"
          type="text"
          error={this.state.errors.longitude || ''}
        />
        <TextFieldGroup label="Altitude"
          onChange={this.onChange}
          placeholder="http://www.example.com"
          value={altitude} field="altitude"
          type="text"
          error={this.state.errors.altitude || ''}
        />

        <div className='btn-group'>
          <hr/>
          <button disabled={isLoading} onClick={this.onClickClearForm}
            className="btn btn-primary btn-sm" style={{marginRight: "15px"}}>
            <span className="glyphicon glyphicon-trash" aria-hidden="true"></span> Clear Form
          </button>
          <button onClick={this.onClickValidate} className="btn btn-primary btn-sm">
            <span className="glyphicon glyphicon-check" aria-hidden="true"></span> Validate Now
          </button>
        </div>
      </form>

    );
  }
}
function validateInput(data) {
  let errors = {};
  let options = { force_decimal: true, decimal_digits: '1,', locale: 'en-US' };
  if (!Validator.isDecimal(data.latitude, options)) {
    errors.uri = 'Not a valid latitude.';
  }
  if (!Validator.isDecimal(data.longitude, options)) {
    errors.uri = 'Not a valid longitude.';
  }

  return {errors, isValid: isEmpty(errors)};
}

export default GeoTypeForm;



