import React from 'react';
import PropTypes from 'prop-types';

// import classnames from 'classnames';
// import Validator from 'validator';
import isEmpty from 'lodash/isEmpty';
import TextFieldGroup from '../../common/TextFieldGroup';
import TextAreaFieldGroup from '../../common/TextAreaFieldGroup';

// var resolvedSchema = require('json-schema-loader!../../../../bower_components/schema/common/json/communication/AddressType.json');

// console.log(resolvedSchema);

class AddressTypeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ...props,
      errors: {},
      isLoading: false
    }
  }

  onChange = (e) => {
    this.props.handleOnChange(e);
  }

  isValid = () => {
    const {errors, isValid} = validateInput(this.state);
    if (!isValid) {
      this.setState({errors});
    }
    return isValid;
  }
  onClickValidate = () => {
    // console.log('onClickValidate');
    const result = validateInput(this.props.data);
    this.setState({...result})
    // console.log('results: ', result);
  }

  onSubmit(e) {
    e.preventDefault();

    // if (this.isValid()) {
    //   this.setState({errors: {}, isLoading: true});
    //   this.setState({isLoading: false});
    // }
  }

  render() {
    var {
      line,
      extendedLines,
      countryCode,
      countrySubDivisions,
      city,
      postalCode,
      postOfficeBox,
      geoLocation,
      formattedAddress
    } = this.props.data;
    var {isLoading} = this.state;

    return (
      <form onSubmit={this.onSubmit}>

        <div className='row'>
          <div className='col-md-12'>
            <TextFieldGroup label="Line 1" onChange={this.onChange} placeholder="line" value={line} field="line" type="text"/>
          </div>
        </div>
        <div className='row'>
          <div className='col-md-12'>
            <TextFieldGroup label="PO Box" onChange={this.onChange} placeholder="postOfficeBox" value={postOfficeBox} field="postOfficeBox" type="text"/>
          </div>
        </div>

        <div className='row'>
          <div className='col-md-12'>
            <label className='control-label' htmlFor='extendedLines'>extendedLines</label>
            <input type='text' className='form-control' name='extendedLines' value={extendedLines} onChange={this.onChange}/>
          </div>
        </div>

        <div className='row'>
          <div className='col-md-3 col-sm-3 col-xs-3'>
            <span className='help-block' htmlFor='city'>City</span>
            <input type='text' className='form-control' name='city' placeholder='city' value={city} onChange={this.onChange}/>
          </div>
          <div className='col-md-3 col-sm-3 col-xs-3'>
            <span className='help-block' htmlFor='countrySubDivisions'>State</span>
            <input type='text' className='form-control' name='countrySubDivisions' placeholder='countrySubDivisions' value={countrySubDivisions} onChange={this.onChange}/>
          </div>
          <div className='col-md-3 col-sm-3 col-xs-3'>
            <span className='help-block' htmlFor='postalCode'>Postal Code</span>
            <input type='text' className='form-control' name='postalCode' placeholder='postalCode' value={postalCode} onChange={this.onChange}/>
          </div>
          <div className='col-md-3 col-sm-3 col-xs-3'>
            <span className='help-block' htmlFor='countryCode'>Country Code</span>
            <input type='text'
              className='form-control'
              width='2' name='countryCode'
              value={countryCode}
              onChange={this.onChange}
            />
          </div>
        </div>

        <div className='row'>
          <div className='col-md-6'>
            <fieldset>
              <legend>geoLocation</legend>
              <div className='row'>
                <div className='col-md-6'>
                  <label htmlFor='latitude' className='control-label'>Latitude</label>
                  <input type='text' className='form-control' name='latitude' value={geoLocation} onChange={this.onChange}/>
                </div>
                <div className='col-md-6'>
                  <label htmlFor='longitude' className='control-label'>Longitude</label>
                  <input type='text' className='form-control' name='longitude' value={geoLocation} onChange={this.onChange}/>
                </div>
              </div>
            </fieldset>
          </div>
          <div className='fieldset col-md-6'>
            <hr/>
            <TextAreaFieldGroup label="Formatted Address" onChange={this.onChange} placeholder="formattedAddress" value={formattedAddress} field="formattedAddress" type="textarea"/>
          </div>
        </div>

        <div className='btn-group'>
          <button disabled={isLoading} onClick={this.onClickClearForm}
            className="btn btn-primary btn-sm" style={{marginRight: "15px"}}>
            <span className="glyphicon glyphicon-trash" aria-hidden="true"></span> Clear Form
          </button>
          <button onClick={this.onClickValidate} className="btn btn-primary btn-sm">
            <span className="glyphicon glyphicon-check" aria-hidden="true"></span> Validate Now
          </button>
        </div>

      </form>

    );
  }
}
function validateInput(data) {
  let errors = {};

  if (isEmpty(data.address)) {
    errors.address = 'This field is required';
  }

  return {errors, isValid: isEmpty(errors)};
}

AddressTypeForm.propTypes = {
  handleOnChange: PropTypes.func
}

AddressTypeForm.defaultProps = {
  data: {
    line: '',
    extendedLines: [''],
    countryCode: '',
    countrySubDivisions: [''],
    city: '',
    postalCode: '',
    postOfficeBox: '',
    geoLocation: {},
    formattedAddress: ''
  }
};
export default AddressTypeForm;



