import React from 'react';

import PhoneTypeForm from './PhoneTypeForm';
import DropLoader from '../../utils/DropLoader';
import ShowJsonText from '../../common/ShowJsonText';

class PhoneTypePage extends React.Component {
  constructor(props) {
      super(props);
      this.state = {
        ...props,
        data: { formattedNumber: ""}
      }
  }
  handleOnChange = (e) => {
    this.setState({
      data: {...this.state.data, [e.target.name]: e.target.value}
    });
  }

  handleClearForm = () => {
    var data = {
      countryDialingCode: "",
      areaDialingCode: "",
      dialNumber: "",
      phoneExtension: "",
      access: "",
      formattedNumber: ""
    };
    this.setState({ data: data });
  }

  handleDropFileInput = (data) => {
    this.setState({ data: data });
  }

  render() {
    return (
      <div>

        <div className='row text-center'>
          <h2>PhoneType</h2>
          <p>[description goes here.]</p>
          <hr/>
        </div>

        <div className="row">
          <div className="col-md-7 col-md-offset-1 well" style={{marginRight: "5px"}}>
            <PhoneTypeForm
              handleOnChange={this.handleOnChange}
              handleClearForm={this.handleClearForm}
            {...this.state} />
          </div>
          <div className="col-md-3 well">
            <ShowJsonText value={this.state.data} />
          </div>
          <DropLoader onChange={this.handleDropFileInput}/>
        </div>
      </div>
    );
  }
}


export default PhoneTypePage;



